.Toolbar {

    height: 56px;
    width: 100%;
    position:fixed;
    top:0;
    left:0;
    background-color: #afcfee;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
    box-sizing: border-box;
    z-index: 90;

}

.Toolbar nav{
    
    height:100%;

}

@media only screen and (max-width:499px){
    .DesktopOnly{
        display:none;
    }
}