
.NavigationItems {

    list-style: none;
    margin:0;
    padding:0;
    display: flex;
    flex-flow:column;
    align-items: center;
    height: 100%;
    
}

@media (min-width:500px){

    .NavigationItems{
        /* ca sa apara pe orizontala itemele */
        flex-flow:row;
    }

}